import React, { PureComponent } from "react";
import { v4 as uuidv4 } from 'uuid';
import Iframe from 'react-iframe';
import ReactPlayer from "react-player"
import PopUpTikTok from './PopUpTikTok'
import { POPUP_VIDEO_RENEW } from '../../../../actions/themeActions'
import { GET_MEDIA_NAME } from "../../../../utils";
const videoIframeTyle = ["youtube", "youtu.be", "vimeo", "tumblr", "soundcloud", "linkedin"]

const isVideo = (videoLink) => {
    let isVideoStatus = false;
    if (String(videoLink)) {
        videoIframeTyle.map((item) => {
            if (String(videoLink).includes(item) && !isVideoStatus) isVideoStatus = true;
        })
    }
    return isVideoStatus;

}
export default class PopUpVideo extends PureComponent {

    render() {
        // console.log('props inside popuVideo', this.props)
        const { data, wall } = this.props;
        return <div className="tb_post_modal_video_holder" key={data.mediaUrl}>
            {
                isVideo(data.link) ? <Iframe
                    className="tb_post_modal_iframe_video"
                    frameBorder="0"
                    allow="accelerometer;fullscreen; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                    src={data.mediaUrl}
                    data-filter-id={data.filterId} data-type="video"
                    data-network={data.network.id}
                    data-link={data.link}
                    data-wall-id={wall.Wall.id} data-item-id={data.id}
                    data-load={0}
                    height="1000"
                    width="1000"
                    autoPlay={true}
                    muted={true}
                    loop={true}
                    playsInline=""
                    allowfullscreen
                ></Iframe> : data.link && String(data.link).includes("facebook") ? <ReactPlayer
                    className="tb_post_modal_iframe_video"
                    poster={data.filename}
                    url={data.mediaUrl} controls data-type="video"
                    data-filter-id={data.filterId}
                    data-network={data.network.id}
                    data-link={data.link}
                    data-wall-id={wall.Wall.id} data-item-id={data.id}
                    data-load={0}
                    autoPlay={true}
                    muted={true}
                    loop={true}
                    playsInline=""
                    height="1000"
                    width="1000"
                /> : (data.network.id == 30) && (data.link.indexOf("vk")) ? <Iframe
                    className="tb_post_modal_iframe_video"
                    frameBorder="0"
                    poster={data.filename}
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                    src={`${data.mediaUrl}&autoplay=1`}
                    allowFullScreen="true"
                    style={{ width: "100%!important" }}
                    data-filter-id={data.filterId}
                    data-type="video"
                    data-network={data.network.id}
                    data-link={data.link}
                    data-wall-id={wall.Wall.id} data-item-id={data.id}
                    data-load={0}
                    autoPlay={true}
                    muted={true}
                    loop={true}
                    playsInline=""
                ></Iframe>
                    // : data.network.id == 28 ? <div className="tb_post_modal_video">
                    //     <PopUpTikTok data={data} key={data.id} />
                    // </div> 
                    :
                    <>
                        {
                            (window.navigator.userAgent.includes('Safari') && !window.navigator.userAgent.includes('Chrome')) && data.mediaUrl.includes('instagram.com') ? <img loading="lazy" className="tb_post_modal_video__"
                                data-wall-id={wall.Wall.id} data-item-id={data.id}
                                data-type="video"
                                data-network={data.network.id}
                                data-link={data.link}
                                data-load={0}
                                data-filter-id={data.filterId} src={data.mediaUrl} autoPlay="" alt={GET_MEDIA_NAME(data.mediaUrl)}
                                muted={true}
                                loop={true}
                                playsInline="" />
                                :
                                <video className="tb_post_modal_video"
                                    poster={data.filename}
                                    data-wall-id={wall.Wall.id} data-item-id={data.id}
                                    data-owner-id={wall.Wall.owner}
                                    data-filter-id={data.filterId}
                                    src={data.mediaUrl}
                                    controls
                                    data-type="video"
                                    data-network={data.network.id}
                                    data-link={data.link}
                                    data-load={0}
                                    autoPlay={true}
                                    muted={true}
                                    loop={true}
                                    playsInline=""
                                    onError={(data.stories == 0) ? (e) => { POPUP_VIDEO_RENEW(e) } : null}
                                ></video>
                        }
                    </>
            }
        </div>
    }
}