import React, { PureComponent } from "react";
import { connect } from 'react-redux';
import PopUpMedia from './Elements/PopUpMedia'
import PopUpAuthor from './Elements/PopUpAuthor'
import PopUpShare from './Elements/PopUpShare'
import PopUpContent from './Elements/PopUpContent'
import { closePopUP, reportMediaPopUp, getDataNextSteps, managePostHeight } from '../../../actions/themeActions'
import PopUpBranding from "./Elements/PopUpBranding";
import { rID } from "../../../utils";

class PostPopUp extends PureComponent {
  state = {
    isEnable: 0,
    eventData: {},
    shareText: "SHARE",
    viewOnText: "View on",
    currentIndex: null,
    completeData: {},
    personalization: {},
    url: null,
    platform: null,
    imgData: [],
    type: "",
    parentID: "",
    isRepeatingPost:false,
    mediaSize:{
      height: 500,
      width: 500,
    }
  }


  keyboardEvent = (event) => {
    let index = this.state.currentIndex
    if(event.keyCode == 39 || event.keyCode == 40){
      index += 1;
      this.featuredPopSliderChangeIndex(index)(event)
    }else if(event.keyCode == 37 || event.keyCode == 38){
      index -= 1;
      this.featuredPopSliderChangeIndex(index)(event)
    }else if(event.keyCode == 27){
      this.closeWidgetPopUP()
    }
  };


  componentDidMount() {
    const { data, wall } = this.props;
    let p_class = document.querySelector('.tb_app_container')
    let P_id = rID ? rID : p_class.parentNode.id
    this.setState({ parentID: P_id })
    this.getWidgetPopUPEvent(data)
    setTimeout(() => {
      var addModal = document.querySelector(`#tb_post_modal_modal-${this.state.parentID}`);
      if (addModal) addModal.classList.add("tb_post_modal_show");

      if (data.personalization.widgetTheme != undefined && data.personalization.widgetTheme == 47) {
        if (addModal) addModal.style.overflow = "hidden";
      }
    })

    var taggBoxRoot = document.getElementsByTagName('html');
    if (taggBoxRoot && taggBoxRoot[0]) {
      taggBoxRoot[0].style.overflow = "hidden";
    }
    this.popUpCloseClckOnWindow()

    window.addEventListener('keydown', this.keyboardEvent);
  }


  componentWillUnmount() {
    window.removeEventListener('keydown', this.keyboardEvent);
  }
  componentDidUpdate() {

    const self = this;
    let modalAction = 0;

    if (window.editor) {
      let modal = document.getElementById("tb_post_modal_modal-taggbox_main");
      let modalBody = document.querySelector(".tb_post_modal_conetent");
      let nextBtn = document.querySelector(".tb_post_modal_nxt_btn");
      let previusButton = document.querySelector(".tb_post_modal_prev_btn");

      if (modal) {

        modalBody.onclick =()=> modalAction =1
        nextBtn.onclick =()=> modalAction =1
        previusButton.onclick =()=> modalAction =1 

        modal.onclick =()=> {
          if(modalAction==0){
            self.onCloseModal()
         }
         modalAction = 0;
        };
      }
    }
  }


  onCloseModal = () => {
    var addModal = document.querySelector(`#tb_post_modal_modal-${this.state.parentID}`);
    if (addModal && addModal.classList.contains("tb_post_modal_show")) {
      addModal.classList.remove("tb_post_modal_show")
      this.setState({ isEnable: 0, currentIndex: null, type: "" }, this.props.closePopUP());
    }

    var taggBoxRoot = document.getElementsByTagName('html');
    if (taggBoxRoot && taggBoxRoot[0]) {
      taggBoxRoot[0].style.overflow = "";
    }
  }
  closeWidgetPopUP = event => this.onCloseModal()

  getWidgetPopUPEvent = (data) => {
    if (data !== undefined && data != "" && data.card) {
      if (data.type == "post") {
        this.setState({
          url: "",
          isEnable: 1,
          eventData: data,
          currentIndex: data.index,
          completeData: data.idArray,
          viewOnText: data.viewOnText,
          shareText: data.shareText,
          imgData: data.imgData,
          personalization: data.personalization,
          type: data.type
        })
      }
      else if (data.type == "submit-pic") {
        this.setState({
          isEnable: 1,
          type: data.type,
          windowParent: data,
        })
      }
    }

  };
  featuredPopSliderChangeIndex = (currentIndex) => event => {
    const { eventData,isRepeatingPost } = this.state;
    if (((eventData.idArray.length-1) == currentIndex || currentIndex === eventData.idArray.length) && !isRepeatingPost) {
      this.loadMore()
      //console.log('loadMore', currentIndex, eventData.idArray.length-1)
    }
    else {
      //console.log('before', currentIndex, eventData.idArray.length-1)
      if (currentIndex === eventData.idArray.length-1 || currentIndex === eventData.idArray.length) {
        this.setState({ 
          currentIndex: 0,
          isRepeatingPost : false
        });
      }else if(currentIndex === -1){
        this.setState({ 
          currentIndex: eventData.idArray.length-1,
          isRepeatingPost : false
        });
      }else{
        this.setState({ 
            currentIndex: currentIndex,
            isRepeatingPost : false
        });
      }
    }
  };
  componentWillReceiveProps(nextProps) {
    const { postData } = nextProps;
    let { currentIndex, completeData, eventData } = this.state
    if (completeData.length != postData.postData.length) {

      // let item = completeData[currentIndex]
      let updatePostData = postData.postData.map(pItem => postData.completeDataObject[pItem])
      let filteredPostData = updatePostData.filter((filterData) => !String(filterData.id).includes("free_add_"))
      // let nIndex = findFromArray(filteredPostData, item.id, currentIndex)
      eventData.idArray = filteredPostData
      this.setState({ completeData: filteredPostData, eventData }, () => {
        const { currentIndex } = this.state;
        this.setState({ currentIndex: currentIndex + 1 })
      })
    }
    else {
      const hasMoreData = postData.hasMoreData && Object.keys(postData.hasMoreData).length > 0 ? postData.hasMoreData[0] && postData.hasMoreData[0].hasMoreData == false ? false : postData.hasMoreData[postData.appendData.networkID] ? postData.hasMoreData[postData.appendData.networkID].hasMoreData : true : true
      if (!hasMoreData) {
        // this.setState({ currentIndex: 0 })
      }
    }



  }
  loadMore = () => {
    const { wall, postData, postHeight } = this.props;
    const { eventData, currentIndex } = this.state;

    const hasMoreData = postData.hasMoreData && Object.keys(postData.hasMoreData).length > 0 ? postData.hasMoreData[0] && postData.hasMoreData[0].hasMoreData == false ? false : postData.hasMoreData[postData.appendData.networkID] ? postData.hasMoreData[postData.appendData.networkID].hasMoreData : true : true
    if(eventData.idArray.length-1 == currentIndex){
      this.setState({isRepeatingPost : true});
    }
    if (hasMoreData) {
      const postCount = wall.ThemeRule.numberOfPosts;
      const timeStamp = Math.floor(Date.now() / 1000);
      this.props.managePostHeight(postHeight);
      this.props.getDataNextSteps(wall.Wall.id, timeStamp, postCount, postData.appendData.networkID, postData.appendData.after, postData.appendData.heightEvent);
    }
    else {
      this.setState({ currentIndex: 0 })
    }

  }
  popUpCloseClckOnWindow = () => {
    document.addEventListener(
      "click",
      (event) => {
        if (event.target.matches(`#tb_post_modal_modal-${this.state.parentID}`)) {
          this.onCloseModal()
        }
      }
    )
  }

  mediaSize = (mediaSize) => {
    this.setState({
      mediaSize:{
        height: mediaSize.height,
        width: mediaSize.width,
      }
    })
  }

  render() {
    const { wall, reportMediaPopUp, languageSetting, networkData, brandingNetworks } = this.props;
    const { viewOnText, isEnable, currentIndex, personalization, completeData, eventData, url, platform, imgData, type, parentID, mediaSize } = this.state;
    let data = null;
    if (currentIndex === -1) data = completeData[eventData.idArray.length - 1];
    else data = completeData[currentIndex];
    const popupSlideShow = (wall.Personalization.popupSlideShow == 1) ? true : false

    return data && parentID && Object.keys(data).length > 0 ? <div className="tb_post_modal_modal" id={`tb_post_modal_modal-${parentID}`}>
      <div className="tb_post_modal_modal_dialog">
        <div className="tb_post_modal_conetent">
          <div className="tb_post_modal_modal_head">
            <div className="tb_post_modal_modal_header">
              <div className="tb_post_modal_post_link_small">
                <div className="tb_post_modal_post_address">
                  {data.link ? <div className="tb_post_modal_view_link" onClick={event => window.open(data.link, "_blank")}>
                    <div className="tb_post_modal_view">{languageSetting.viewOnText} {data.network.name}</div>
                    <div className="tb_post_view_ico tb__icon tb-arrow-right-alt"> </div>
                  </div> : null}
                </div>
              </div>
              <div className="tb_post_modal_close_wrap">
                <div className="tb_post_modal_close_btn" onClick={this.closeWidgetPopUP}>
                  <div className="tb_post_close_ico tb__icon tb-close-alt"> </div>
                </div>
              </div>
            </div>
            {popupSlideShow ? <div className={`tb_post_modal_navigation tb_post_modal_nav_content ${data.type === 1 ? 'tb_post_modal_text_nav__' : null}`}>
              <div className="tb_post_modal_nav tb_post_modal_prev_btn" onClick={this.featuredPopSliderChangeIndex(currentIndex - 1)}>
                <div className="tb_post_nav tb__icon tb-arrow-left-alt"> </div>
              </div>
              <div className="tb_post_modal_nav tb_post_modal_nxt_btn" onClick={this.featuredPopSliderChangeIndex(currentIndex + 1)}>
                <div className="tb_post_nav tb__icon tb-arrow-right-alt"> </div>
              </div>
            </div> : null}
          </div>
          <div className="tb_post_modal_modal_body" style={{ backgroundColor: wall.ThemeRule.cardColor }}>
            {data.type != 1 ? <div className="tb_post_modal_details_left">
              {popupSlideShow ? <div className="tb_post_modal_navigation tb_post_modal_nav_img">
                <div className="tb_post_modal_nav tb_post_modal_prev_btn" onClick={this.featuredPopSliderChangeIndex(currentIndex - 1)}>
                  <div className="tb_post_nav tb__icon tb-arrow-left-alt"> </div>
                </div>
                <div className="tb_post_modal_nav tb_post_modal_nxt_btn" onClick={this.featuredPopSliderChangeIndex(currentIndex + 1)}>
                  <div className="tb_post_nav tb__icon tb-arrow-right-alt"> </div>
                </div>
              </div> : null}
              <PopUpMedia data={data} wall={wall} mediaSizeCallback={this.mediaSize} />
            </div> : null}
            <div className={`tb_post_modal_details_right ${data.type === 1 ? 'tb_post_modal_text__' : ''}`} style={{ backgroundColor: data.font.cardColor }}>
              <div className="tb_post_modal_content_wrap">
                <div className="tb_post_modal_post_detail_wrap">
                  <PopUpAuthor key={`author_${data.id}`} item={data} ThemeRule={wall.ThemeRule} Personalization={wall.Personalization} ownerId={wall.Wall.owner} />
                  <PopUpContent key={data.id} ThemeRule={wall.ThemeRule} Personalization={wall.Personalization} item={data} contentData={data.content} contentTitle={data.contentTitle} mediaSize={mediaSize} />
                </div>
                <div className="tb_post_modal_post_footer">
                  <div className="tb_post_modal_post_footer_report" onClick={event => reportMediaPopUp(data)}>
                    <div className="tb_post_modal_post_report_media_btn_wrap__">
                      <div className="tb_post_modal_post_report_media_btn__" style={{
                        color: data.font.fontColor
                      }}>Report</div>
                    </div>
                  </div>
                  <PopUpShare item={data} wall={wall} color={wall.ThemeRule.fontColor} />
                </div>
              </div>
            </div>
            {wall.UserRule.branding_lite && wall.UserRule.branding_lite == 1 ? <PopUpBranding brandingNetworks={brandingNetworks} languageSetting={languageSetting} wallId={wall.Wall.id} /> : null}
          </div>
        </div>
      </div>
    </div> : null


  }
}
const mapStateToProps = state => {

  return {
    loaderData: state.loaderData,
    postHeight: state.postHeight.data
  }
}
const mapDispatchToProps = dispatch => {
  return {
    closePopUP: (data) => dispatch(closePopUP(data)),
    reportMediaPopUp: (data) => dispatch(reportMediaPopUp(data)),
    managePostHeight: (data) => dispatch(managePostHeight(data)),
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow))

  }
}
export default connect(mapStateToProps, mapDispatchToProps)(PostPopUp);