import React from 'react';
const AuthorAvatar = ({ username, network }) => {
    return <img className={`tb_nc_author_profile__`} src={`https://ui-avatars.com/api/?name=${username.replace(/\s/g, '')}&background=${String(network.id === null ? `#000` : network.color).replace("#", '')}&color=fff&length=1`} alt={username} width={44} height={44} />
}

export default AuthorAvatar;