import React, { PureComponent } from "react";
import { POPUP_IMAGE_RENEW_REQUEST } from '../../../../actions/themeActions'
import { GET_MEDIA_NAME } from "../../../../utils";

const mediaDimension = JSON.parse(window.localStorage.getItem('mediaDimension'));
export default class PopUpImage extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      mediaSize:{
        height:mediaDimension != null ? mediaDimension[0] : 650,
        width:mediaDimension != null ? mediaDimension[1] : 650,
      },
      mediaLoaded:false,
      imgUrl: props.ImageUrl
    };
    this.mediaRef = React.createRef();
  }

  componentWillReceiveProps(nextProps) {
    const { ImageUrl } = nextProps;
    if(this.state.imgUrl !== ImageUrl){
      this.setState({ imgUrl: ImageUrl,mediaLoaded:false })
    }
  }

  componentDidMount() {
    window.addEventListener('resize', this.updateImageDimensions);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateImageDimensions);
  }

  updateImageDimensions = () => {
    if (this.mediaRef.current) {
      if (this.mediaRef.current.getAttribute("data-load") === 1) { 
        this.setState({ imgUrl: this.mediaRef.current.src })
      }
      const mediaDimensionUpdate = [this.mediaRef.current.offsetHeight, this.mediaRef.current.offsetWidth];
      window.localStorage.setItem('mediaDimension', JSON.stringify(mediaDimensionUpdate));

      this.setState({ 
        mediaLoaded:true,
        mediaSize:{
          height:this.mediaRef.current.offsetHeight,
          width:this.mediaRef.current.offsetWidth
        }
        },()=>this.props.mediaSizeCallback({
          height: this.state.mediaSize.height,
          width: this.state.mediaSize.width
        }));
    }
  };

  render() {
    const { data, wall, opacity } = this.props
    const { imgUrl, mediaSize, mediaLoaded} = this.state
    return (
        <div className={`tb_post_modal_img_holder${!mediaLoaded ? ' tb_media_modal_spinner':''}`} style={{minHeight:!mediaLoaded?mediaSize.height:null, minWidth:!mediaLoaded?mediaSize.width:null}}>
          <img 
          className="tb_post_modal_img_" 
          decoding="async" 
          ref={this.mediaRef} 
          src={imgUrl} 
          //srcset={`${imgUrl} 2x`}
          alt={GET_MEDIA_NAME(imgUrl)} 
          data-link={data.link} 
          data-network={data.network.id} 
          data-wall-id={wall.Wall.id} 
          data-owner-id={wall.Wall.owner} 
          data-item-id={data.id} 
          data-filter-id={data.filterId} 
          onLoad={this.updateImageDimensions} 
          onError={(data.stories == 0) ? (e) => {POPUP_IMAGE_RENEW_REQUEST(e)} : null} 
          key={imgUrl} 
          loading="lazy" 
          draggable="false"
          style={{opacity:mediaLoaded ? opacity : 0}} />
        </div>
    );
  }
}

