import React, { PureComponent } from "react";
import DefaultNetwork from "./DefaultNetwork";

export default class Network extends PureComponent {
  render() {
    const { network, networkClass, ThemeRule, font, isPopUp } = this.props;

    const isDefaultIcon = ThemeRule.iconType === 1 ? (network.id === 7 || network.id === 4) ? false : true : false

    return (isDefaultIcon ? <div className={`${networkClass} tb__icon tb-${network.icon}`} style={{ color:font.iconColor }}><div></div></div> : <DefaultNetwork network={network} networkClass={networkClass} />);
  }
}
